	module.exports = {"data":{"markdownRemark":{"html":"<p>Technica is the largest all ladies hackathon in the world, held annually at the University of Maryland, College Park.</p>\n<p>In 2015 I helped organize the first Technica hackathon. I was in finding mentors and volunteers and organizing them during the event. I also raised $5,000 in funding and helped manage logistics. </p>\n<p>Mentors:</p>\n<p>I reached out to hundreds of mentors in the DC-area and beyond and got 50 excited people to sign up to mentor our hackers.</p>\n<p>Volunteers:</p>\n<p>Coordinating volunteers took several months of planning. </p>\n<p>Once the event drew close we held a series of volunteer interest meetings where we got people excited about the event and how they could help. In the week before the event I led walkthroughs of the venue in order to make sure that volunteers were familiar with the venue and ready to help out on the day of.</p>\n<p>Once the day of the event came, we welcomed over 500 attendees for our 24 hour event. </p>\n<p>Bonus: <a href=\"https://docs.google.com/presentation/d/116S7IZUc6esXZX-k3WWgjzdffxnpBE1gFtdgTFAnsHo/edit?usp=sharing\">Tech+X GitHub</a> an introductory GitHub workshop I gave around Halloween time during the lead up to the second Technica.</p>","frontmatter":{"date":"January 01, 2015","path":"/technica","title":"Technica"}}},"pathContext":{}}

